import { React, useState, useEffect } from "react";
import { ApiGet, ApiDelete } from "@services";
import "@assets/scss/setting.scss";
import { LogForms, GlobalPopup } from "@components";
import { deleteIcon, editIcon } from "@assets/img";
import { useNavigate } from "react-router-dom";

//data
import logDatas from "../../_apiDatas/settings/logData.json";

export const Log = () => {
    const navigate = useNavigate();
    const [totalData, setTotalData] = useState([]);
    const [flag, setFlag] = useState(true);
    const handleFlag = (val) => setFlag(true);

    // modal 
    const [modals, showModal] = useState(false);
    const setModalStatus = (childVal) => showModal(childVal);
    // popup 
    const [popups, showPopup] = useState(false); //  show-hide
    const setPopupStatus = (childVal) => showPopup(childVal); //   show-hide 
    const [btns, setBtnVal] = useState(false); //  
    const btnValue = (popVal) => setBtnVal(popVal); //   
    const [keys, setKeys] = useState();

    const [btnType, setBtnType] = useState();
    const [logData, setLogData] = useState([]);

    const getLogData = async () => {
        // const res = await ApiGet('api_v1/system/settings/logs', navigate);
        const res = logDatas;
        setTotalData(res.logsStatus);
    }

    const deleteLogData = async () => {
        // let res = await ApiDelete(`api_v1/system/settings/logs?key=${keys}`);
        let res = {"code": 200};
        if (res.code === 200) {
            alert(' ');
            getLogData();
            setBtnVal(false);
        }
        else alert(' .')
    }

    const deleteLogSettings = (logData) => {
        showPopup(!popups); //  
        setKeys(logData);
    };

    useEffect(() => {
        if (btns) deleteLogData(); // -  
    }, [btns]);

    useEffect(() => {
        if (flag) {
            getLogData()
            setFlag(false)
        }
    }, [flag])

    return (
        <div className="setting_page">
            {modals && <LogForms isShow={setModalStatus} title={""} logData={logData} eventType={btnType} handleFlag={handleFlag} showValue={modals}/>}
            {popups && <GlobalPopup innerText={'    ?'} btnType={"all"} isShow={setPopupStatus} isResult={btnValue} />}
            <p className="setting_title">  </p>
            <div className="ac_top_container">
                <button onClick={() => {
                    setBtnType('insert');
                    showModal(!modals);
                }} className="log"></button>
                <p>  : {totalData.length} </p>
            </div>
            <ul className="system_table ac_table">
                <li className="system_header">
                    <p>
                        <img src={editIcon} alt="edit_icon" />
                    </p>
                    <p>
                        <img src={deleteIcon} alt="del_icon" />
                    </p>
                    <p>IP</p>
                    <p>(UDP)</p>
                </li>
                {totalData && totalData.map((item, index) => {
                    return (
                        <li key={item.key}>
                            <p>
                                <button
                                    className="edit_btn"
                                    onClick={() => {
                                        setLogData(item)
                                        setBtnType('update');
                                        showModal(!modals);
                                    }} />
                            </p>
                            <p><button className="del_btn" onClick={() => deleteLogSettings(item.key)} /></p>
                            <p>{item.ip}</p>
                            <p>{item.port}</p>
                        </li>
                    );
                })}
            </ul>
        </div>
    )
}