import { React, useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { SelectBox } from "@components";
import { Loading } from "@components";
import { ApiGet, ApiPost, ApiDelete } from "@services";

//data
import diskInfoData from "../../_apiDatas/settings/diskInfo.json"

const host = 'https://kwpapi.kwp.lab/'   //'http://localhost:8000/'
const diskCleanList = [
    {
        name: "6 ",
        value: "6"
    },
    {
        name: "1 ",
        value: "12"
    },
    {
        name: "2 ",
        value: "24"
    },
];

const downloadList = [
    {
        name: "1 ",
        value: "12"
    },
    {
        name: "2 ",
        value: "24"
    },
    {
        name: "3 ",
        value: "36"
    },
];

const findValueIndex = (list, value) => {
    let result = null;
    list.map((item, index) => 
        item.value === value ? result = index : null
    )
    return result;
}


export const Storage = () => {
    const navigate = useNavigate();
    const [delProgress, setDelProgress] = useState(false);
    const [dwnProgress, setDwnProgress] = useState(false);
    const [deleteValue, setDeleteValue] = useState(``);             // 
    const [downValue,setDownvalue] = useState(``);      // 
    const [diskInfo, setDiskInfo] = useState({
        'dbTotal': '',
        'dbUsage': '',
        'dbRate': '',
        'systemTotal': '',
        'systemUsage': '',
        'systemRate': '',
    });

    const getDiskInfo = async () => {
        // let res = await ApiGet(`api_v1/system/settings/diskInfo`, navigate);

        let res = diskInfoData;
        if (res.code === 200) {
            res.diskStatus.map((item, index) => {
                setDiskInfo((prev) => {
                    return {
                        ...prev,
                        [item.diskname]: item.value
                    }
                });
            });
        };
    };

    const deleteDisk = async () => {
        if (deleteValue !== '') {
            setDelProgress(true);
            const res = {code : 200};
            // const res = await ApiDelete(`api_v1/system/settings/deleteDisk?period=${deleteValue}`, navigate);
            if (res.code === 200) {
                getDiskInfo();
                alert("  ")
            }
            setDelProgress(false);
        } else {
            alert("   .")
        }
    };

    const downloadCsv = async () => {
        if (downValue !== '') {
            setDwnProgress(true);
            alert(" .")
            // const res = await ApiPost(`api_v1/system/settings/savecsv?period=${downValue}`, navigate);
            // if (res.code === 200) window.open(host+'api_v1/system/settings/download/tagCsv')
            // window.open(host+'api_v1/system/settings/download/tagCsv')
            setDwnProgress(false);
        } else {
            alert("    .")
        }
    }
    useEffect(() => {
        getDiskInfo();
    }, []);

    return (
        <div className="setting_page">
            <p className="setting_title"> </p>
            <div className="info_line">
                <p className="sub_title">DB </p>
                <p className="result"><span className="bold">{diskInfo.dbRate}</span> ({diskInfo.dbUsage} / {diskInfo.dbTotal}  )</p>
            </div>
            <div className="info_line">
                <p className="sub_title"> </p>
                <p className="result"><span className="bold">{diskInfo.systemRate}</span> ({diskInfo.systemUsage} / {diskInfo.systemTotal}  )</p>
            </div>
            <div className="info_line">
                <p className="sub_title" style={{ lineHeight: "40px" }}> </p>
                {delProgress && <Loading isFull={true} width={"100%"} height={"100%"} bg={false}/>}
                <div className="result">
                    <SelectBox innerList={diskCleanList} handleValue={setDeleteValue} defaultIndex={findValueIndex(diskCleanList, '')} label={' '}/>
                    <button className="action_btn del_btn" onClick={deleteDisk}></button>
                </div>
            </div>
            <p className="notice">       .</p>
            <div className="line"/>
            <p className="setting_title"> </p>
            <div className="info_line">
                <p className="sub_title" style={{ lineHeight: "40px" }}>  </p>
                {dwnProgress && <Loading isFull={true} width={"100%"} height={"100%"} bg={false}/>}
                <div className="result">
                    <SelectBox innerList={downloadList} handleValue={setDownvalue} defaultIndex={findValueIndex(downloadList, '')} label={' '}/>
                    <button className="action_btn save_btn" onClick={downloadCsv}></button>
                </div>
            </div>
            
        </div>
    )
}