/* eslint-disable react-hooks/exhaustive-deps */
import { React, useState } from "react";
import { useNavigate } from "react-router-dom";
import "@assets/scss/setting.scss";
import { deleteIcon, editIcon, mailO, mailX } from "@assets/img";
import { AccountModal, Pagination, GlobalPopup } from "@components";
import { ApiGet, ApiDelete } from "@services";
import { useEffect } from "react";

//data
import userListData from "../../_apiDatas/settings/userList.json"

export const Account = () => {
    // pagination & userList ()
    const [pageSize, setPageSize] = useState(10); //  10 
    const [currentPage, setCurrentPage] = useState(1); // 
    const [totalCount, setTotalCount] = useState(0); //   
    const [userList, setUserList] = useState([]); //   
    // 
    const navigate = useNavigate();
    // modal 
    const [modals, showModal] = useState(false); //  show-hide
    const setModalStatus = (childVal) => showModal(childVal); //    show-hide
    // popup 
    const [popups, showPopup] = useState(false); //  show-hide
    const setPopupStatus = (childVal) => showPopup(childVal); //   show-hide 
    const [btns, setBtnVal] = useState(false); //  
    const btnValue = (popVal) => setBtnVal(popVal); //   
    const [delUser, delUserId] = useState(); //   id
    // 
    const [userData, getUserData] = useState([]);
    // ()  
    const [btnType , getBtnType] = useState();
    // 
    const getUserList = async () => {
        let offValue = pageSize * (currentPage - 1);

        // let userList = await ApiGet(`/api_v1/system/register/users?limit=${pageSize}&offset=${offValue}`, navigate); //   
        // let totalList = await ApiGet(`/api_v1/system/register/users?`, navigate); //  

        let userList = userListData;
        let totalList = {
            "userList": 2,
            "code": 200
        }

        setUserList(userList.userList);
        setTotalCount(totalList.userList);
    }

    //  
    const deleteUser = async () => {
        // let res = await ApiDelete(`/api_v1/system/register/users?id=${delUser}`, navigate);
        let res =  {"code": 200};

        if (res.code === 200) {
            alert(' ')
            getUserList();
            setBtnVal(false);
        }
        else alert(' .')
    }

    const deleteUserInfo = (userId) => {
        showPopup(!popups); //  
        delUserId(userId); //  id 
    }

    useEffect(() => {
        getUserList(); // 
        if (btns) deleteUser(); // -  
    }, [pageSize, currentPage, btns, delUser]);

    return (
        <div className="setting_page">
            {modals && <AccountModal isShow={setModalStatus} title={" "} evtType={btnType} innerData={userData} tableUpdate={getUserList}/>}
            {popups && <GlobalPopup innerText={'   ?'} btnType={"all"} isShow={setPopupStatus} isResult={btnValue} />}

            <p className="setting_title"> </p>
            <div className="ac_top_container">
                <button
                    onClick={() => {
                        showModal(!modals);
                        getUserData([]);
                        getBtnType('add');
                    }}
                    className="add_user">
                </button>
                <p>  : {totalCount} </p>
            </div>
            <ul className="system_table ac_table">
                <li className="system_header">
                    <p><img src={editIcon} alt="edit_icon" /></p>
                    <p><img src={deleteIcon} alt="del_icon" /></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                    <p></p>
                </li>

                {userList && userList.map(item => {
                    const mailIcon = item.alarm === "true" ? mailO : mailX;
                    return (
                        <li key={item.id}>
                            <p>
                                <button 
                                    className="edit_btn" 
                                    onClick={() => {
                                        getUserData(item);
                                        showModal(!modals);
                                        getBtnType('update');
                                }}/>
                            </p>
                            <p>
                                <button className="del_btn" onClick={() => deleteUserInfo(item.idname)}></button>
                            </p>
                            <p>{item.name}</p>
                            <p>{item.idname}</p>
                            <p>{item.role}</p>
                            <p>{item.email}</p>
                            <p><img src={mailIcon} alt="mail_icon" /></p>
                            <p>{item.desc}</p>
                        </li>
                    );
                })}
            </ul>
            <Pagination
                total={totalCount}
                current={currentPage}
                setCurrent={setCurrentPage}
                pageSize={pageSize}
                setPageSize={setPageSize}
                onChange={setCurrentPage}
                useSelect={false}
            />
        </div>
    )
}